.projects {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.container {
  margin: 50px 0;
  width: 80%;
}

.listMyProjects {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: 60px;
}

@media screen and (max-width: 691px) {
  .container {
    margin: 30px 0;
  }
  .listMyProjects {
    margin-top: 40px;
  }
}
